import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, TableData } from 'components/common/index';
import { getPaperStudent } from 'services/paperStudent';

class ReturnDocPage extends Component {
  state = {
    reload: 0,
  };

  render() {
    const columns = [
      {
        title: 'Mã sv',
        dataIndex: 'studentId',
        key: 'studentId',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'studentId',
        key: 'studentId',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'birthdate',
        key: 'birthdate',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <CartHeadAction>
            <BtnAddNew href="#/studentTitle/form/new" />
          </CartHeadAction>
        ),

        // returnDoc.length >= 1 ? (
        //   <Fragment>
        //     <Button
        //       className="btnNoiNgoaiTru"
        //       type="primary"
        //       href={`#return-doc/form/${record.code}`}
        //     >
        //       Hoàn trả
        //     </Button>
        //   </Fragment>
        // ) : null,
      },
    ];
    return (
      <Card title="Trả giấy tờ">
        {/* <Row>
          <Col span={24}>
            <BtnAddNew href="#/teacher/form/new" />
          </Col>
        </Row> */}
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPaperStudent}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReturnDocPage />
  </Page>
);



